import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Link} from 'react-router-dom';
const Home = ( )=>
{
    const [users,setUser] = useState([]);
    const [url,setUrl] = useState("");
    

    useEffect(()=>{
        loadUsers()
    },[]);

    const loadUsers = async ()=>{
        const result = await axios.get("http://localhost:3001/users");
        setUser(result.data.reverse());
    }

    const deleteUser = async (id) =>{
        await axios.delete(`http://localhost:3001/users/${id}`);
        loadUsers();
    }



    const abc =  (id) =>{
      var pass = Number(prompt("Please your ID"));
      console.log(id);
      if(pass === id)
      {
        setUrl(`/users/edit/${id}`) ;
      }
      else{
        alert("Wrong ID")
       setUrl('/') ;
      }
      
    }
    

  return(
    <>
    <div className="container">
    <div className="py-4">
    <h1>Dashboard</h1><br/>
    <table class="table border shadow">
  <thead class="thead-dark">
    <tr>
      <th scope="col">#</th>
      <th scope="col">Name</th>
      <th scope="col">Username</th>
      <th scope="col">Email</th>
      <th>Action</th>
    </tr>
  </thead>
  <tbody>
  {
      users.map((user, index)=> (
          <tr>
          <th scope="row">{index+1}</th>
          <td>{user.name}</td>
          <td>{user.username}</td>
         <td>{user.email}</td>
         <td>
             <Link className="btn btn-primary mr-2" to={`/users/${user.id}`}>View</Link>
             <Link className="btn btn-outline-primary mr-2" onClick={()=>abc(user.id)} to={url} >Edit </Link>
             <Link className="btn btn-outline-danger" onClick={()=> deleteUser(user.id)}>Delete</Link>

         </td>
         </tr>
      ))
  }
  </tbody>
</table>


    </div>
    </div>
      
    </>


  )
}

export default Home;
